/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Staatliches", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Staatliches", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.nav__link {
  margin-bottom: 2rem;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
.nav__link:hover {
  color: #B8336A;
}
@media (min-width: 768px) {
  .nav__link {
    margin: 0;
  }
}
.nav__link--active {
  font-weight: 700;
  color: #B8336A;
}
@media (min-width: 768px) {
  .nav__link--active::after {
    content: "";
    display: inline-block;
    height: 4rem;
    border-right: 2px solid #F2F2F2;
    margin: 0 0 0 2rem;
  }
}

.hero {
  min-height: 60rem;
  background-image: url(../img/hero.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #FFFFFF;
}
.hero__text {
  max-width: 60rem;
}
.hero__link {
  background-color: #ff8015;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .hero__link {
    width: auto;
  }
}
.hero__link:hover {
  background-color: #e16700;
}

.search {
  background-color: #F2F2F2;
  padding: 5rem 0;
}
.search__heading {
  text-align: center;
  color: #484349;
  margin: 0;
}

.form {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .form {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.form__field {
  padding: 1rem;
  border: 0.1rem solid #c7c3c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .form__field {
    margin: 0;
  }
}
.form__submit {
  background-color: #B8336A;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin-top: 0;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
.form__submit:hover {
  background-color: #902853;
}